import { TestState } from 'models/tests/types';
import * as Yup from 'yup';

export const signUpValid = Yup.object().shape({
  username: Yup.string()
    .transform((value) => (value ? value.trim() : value))
    .required(' ')
    .test('notOnlyWhitespace', ' ', (value) => {
      return /\S/.test(value);
    })
    .min(3, '   3 ')
    .max(50, '   50 '),
  password: Yup.string()
    .transform((value) => (value ? value.trim() : value))
    .required(' ')
    .test('notOnlyWhitespace', ' ', (value) => {
      return /\S/.test(value);
    })
    .min(6, '   6 ')
    .max(50, '   50 '),

  password_confirmation: Yup.string()
    .transform((value) => (value ? value.trim() : value))
    .required('   ')
    .test('notOnlyWhitespace', ' ', (value) => {
      return /\S/.test(value);
    })
    .min(6, '   6 ')
    .max(50, '   50 '),
});

