export const answerValid = (question_type: string) =>
  Yup.object().shape({
    text:
      question_type !== 'number'
        ? Yup.string()
            .transform((value) => (value ? value.trim() : value))
            .required(' ')
            .test('notOnlyWhitespace', ' ', (value) =>
              /\S/.test(value)
            )
            .min(1, '   1 ')
            .max(30, '   30 ')
        : Yup.string()
            .required(' ')
            .matches(/^[0-9]+$/, '  '),
  });

export const questionValid = (
  question_type: string,
  answer: number,
  countIsRight: number,
  setErrorText?: React.Dispatch<React.SetStateAction<string>>
) => {
  if (question_type !== 'number' && answer < 2) {
    setErrorText && setErrorText('     2');
    return false;
  }
  switch (question_type) {
    case 'single':
      if (countIsRight !== 1) {
        setErrorText && setErrorText('   ');
        return false;
      }
      break;

    case 'multiple':
      if (countIsRight < 1) {
        setErrorText && setErrorText('  ');
        return false;
      }
      break;

    case 'number':
      if (answer < 1) {
        setErrorText && setErrorText(' ');
        return false;
      }
      break;
  }
  setErrorText && setErrorText('');
  return true;
};

export const validTest = (test: TestState) => {
  return (
    test.questions.length !== 0 &&
    test.questions.every((question) => {
      const countIsRight =
        question?.answers?.reduce(
          (count, answer) => (answer.is_right ? count + 1 : count),
          0
        ) || 0;
      const isValid = questionValid(
        question.question_type,
        question.answer,
        countIsRight
      );
      return isValid;
    })
  );
};
